import React from "react";
import s from "./Cart.module.scss";
import CartIcon from "./CartIcon";
import { A } from "hookrouter";

const Cart = () => {
  return (
    <div className={s.Cart}>

        <CartIcon />
        <A className={s.link} draggable={false} href="/basket">
        </A>
    </div>
  );
};

export default Cart;
